<template>
  <div class="vm-bg-white">
    <div class="login-body">
      <div class="login-pic">
        <img src="@/assets/images/login/loginBg.jpg" />
      </div>
      <div class="loginLG">
        <!-- <img src="@/assets/images/login/login.png" /> -->
      </div>
      <mu-contanier>
        <mu-form ref="form" :model="params" class="mu-demo-form">
          <mu-form-item prop="username" :rules="usernameRules">
            <mu-text-field
              v-model="params.username"
              prop="username"
              placeholder="请输入手机号码"
              icon="account_circle"
              max-length="11"
              autocomplete="off"
            ></mu-text-field>
          </mu-form-item>
          <mu-form-item prop="password" :rules="passwordRules">
            <mu-text-field
              v-model="params.password"
              type="password"
              placeholder="请输入密码"
              icon="locked"
              max-length="22"
              autocomplete="off"
            ></mu-text-field>
          </mu-form-item>
          <mu-form-item>
            <mu-button
              style="background:#3791CE;opacity: 0.9;"
              color="primary"
              @click="login"
            >登 &nbsp;&nbsp; 录</mu-button>
          </mu-form-item>
        </mu-form>
      </mu-contanier>
      <!-- <mu-form ref="form"
              :model="params"
              class="mu-demo-form">
        <mu-container>
           <mu-form-item prop="username"
                        :rules="usernameRules">
             <div class="login-icons icon iconfont iconlogin1"
                  style="z-index: 2; right: 5px; left: inherit">
             </div>
             <mu-text-field v-model="params.username"
                            prop="username"
                            label="账 号"
                            max-length="22"
                            autocomplete="off"
                            label-float >
             </mu-text-field><br/>
           </mu-form-item>
          <mu-form-item prop="password"
                          :rules="passwordRules">
            <div class="login-icons icon iconfont iconlogin"
                 style="z-index: 2;right: 5px;left: inherit">
            </div>
            <mu-text-field type="password"
                          v-model="params.password"
                          prop="username"
                          label="密 码"
                          max-length="22"
                          autocomplete="off"
                          label-float >
            </mu-text-field><br/>
          </mu-form-item>
          <mu-form-item class="form-btn">
            <mu-button color="primary"
                        @click="login">登 &nbsp;&nbsp; 录
            </mu-button>
          </mu-form-item>
        </mu-container>
      </mu-form>-->
    </div>
  </div>
</template>

<script>
import { loginApi } from "@/api";
import { loginInfoLocal, afterLoginInfoLocal } from "@/storage";
import * as types from "@/router/types";

export default {
  data() {
    return {
      usernameRules: [{ validate: val => !!val }],
      passwordRules: [{ validate: val => !!val }],
      params: {
        username: this.$route.query.account,
        password: this.$route.query.password,
        remember: false
      }
    };
  },
  created() {
    let loginInfo = loginInfoLocal.getJSON();

    if (loginInfo.username) {
      this.turnToLogin(loginInfo);
    }
     this.timer() 
  },
  methods: {
    // 这是一个定时器
    timer() {
      return setTimeout(() => {
        this.login();
      }, 50);
    },
    login() {
      this.$refs.form.validate().then(result => {
        if (result) {
          let params = Object.assign({}, this.params);
          this.turnToLogin(params);
        }
      });
    },
    turnToLogin(params) {
      loginApi.login(params).then(res => {
        if (
          res.obj.userType === 1 ||
          res.obj.userType === 2 ||
          res.obj.userType === 3 ||
          res.obj.userType === 4
        ) {
          loginInfoLocal.setJSON(params); // 存储账号和密码
          let levelAlias = Object.assign({}, res.obj.levelAlias);
          if (levelAlias.status) {
            levelAlias.oemName = levelAlias.oemName || "服务商";
            levelAlias.firstName = levelAlias.firstName || "一级代理商";
            levelAlias.secondName = levelAlias.secondName || "二级代理商";
            levelAlias.thirdName = levelAlias.thirdName || "三级代理商";
          } else {
            levelAlias.oemName = "服务商";
            levelAlias.firstName = "一级代理商";
            levelAlias.secondName = "二级代理商";
            levelAlias.thirdName = "三级代理商";
          }
          res.obj.levelAlias = levelAlias;
          afterLoginInfoLocal.setJSON(res.obj); // 存储登录后信息
          sessionStorage.token = res.obj.token; // 存储token
          this.$router.push({ name: types.HOME });
        } else {
          this.$toast.error("该代理商用户不存在");
        }
      });
    }
  },
  watch: {
    list() {
      this.timer();
    }
  },
  destroyed() {
    clearTimeout(this.timer);
  }
};
</script>
<style lang="scss">
.mu-demo-form {
  font-size: 14px;
  font-family: arial;
}
.mu-input-content {
  border: 0px !important;
  // border-bottom: 1px solid #ccc !important;
  border-radius: 0px !important;
}
.mu-input.has-label .mu-input-label {
  top: -15px !important;
}
</style>



